<script>
export default {
  data() {
    return {
      canvas: undefined,
      ctx: undefined,
      play: false
    };
  },
  methods: {
    audioSwitcher() {
      this.play = !this.play
      if (this.play) 
        this.$player.play()
      else
        this.$player.pause()
    }
  },
  mounted() {
    this.canvas = document.getElementById("neon-bg")
    this.ctx = this.canvas.getContext("2d")
    this.ctx.filter = "blur(25px)"
    this.ctx.fillStyle = "rgba(19, 158, 236, 0.41)"
    this.ctx.fillRect(
      this.canvas.width / 4,
      this.canvas.height / 4,
      this.canvas.width / 2,
      this.canvas.height / 2
    );
    this.ctx.fill()
  }
};
</script>


